import { NGL3dApp } from '..';

import { MeshBase } from '../Mesh/Meshes/Core/Base';

export class Scene {
    NGL3dApp: NGL3dApp;

    geometries: MeshBase[] = [];

    constructor(NGL3dApp: NGL3dApp) {
        this.NGL3dApp = NGL3dApp;
    }
    add(mesh: MeshBase) {
        this.geometries.push(mesh);
    }
}